.forecast {
  width: 90%;
  height: 70%;
  background-color: #dae1e1;
  background-image: linear-gradient(rgba(113, 113, 113, .506), rgba(113, 113, 113, .506)), url("bg-img.2bbbf86a.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 1px 1px 9px 3px #fff;
}

.forecast__query {
  justify-content: center;
  align-items: center;
  display: flex;
}

.forecast__loc-input {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 30px;
  outline: none;
  padding: 12px;
  position: relative;
}

.forecast__loc-input + label {
  color: rgba(0, 0, 0, .306);
  cursor: text;
  transition: all .35s cubic-bezier(0, 0, .2, 1);
}

.forecast__loc-input:focus + label {
  height: 50%;
  font-size: 12px;
  transform: translateY(-100%);
}

.forecast__days-select {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 30px;
  outline: none;
  padding-left: 5px;
}

